<template>
    <section class="link-redirect-section">
      <h1 class="header-redirect-title">Use short link</h1>
      <form class="redirect-form" @submit.prevent="handleSubmit">
        <input v-model="shortLink" type="url" name="short_link" id="short_link" placeholder="Just add short link">
        <div class="buttons-container">
          <button type="reset" class="redirect-clear-button clear-button blue-button">Reset</button>
          <button type="submit" class="redirect-submit-button submit-button green-button">Go to URL</button>
        </div>
      </form>
    </section>
  </template>
  
  <script>
  export default {
    data() {
      return {
        shortLink: ''
      }
    },
    methods: {
      handleSubmit() {
        // Handle form submission, e.g., navigate to the short link URL
        console.log('Short link submitted:', this.shortLink);
        // You can use window.location or Vue Router to navigate to the short link URL
        // Example using window.location:
        window.location.href = this.shortLink;
      }
    }
  }
  </script>
  
  <style scoped>
  .link-redirect-section {
    min-height: 200px;
    width: 50%;
  }
  
  .header-redirect-title {
    display: flex;
    justify-content: center;
    align-items: center;
    color: darkslateblue;
  }
  
  .redirect-form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    min-height: 100px;
  }
  
  #short_link {
    min-width: 280px;
  }
  
  .buttons-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  
  .clear-button,
  .submit-button {
    min-width: 140px;
    border-radius: 25px;
  }
  
  .blue-button {
    background-color: lightblue;
    border-color: aqua;
  }
  
  .green-button {
    background-color: lightgreen;
    border-color: greenyellow;
  }
  
  button {
    font-weight: 600;
  }
  </style>